html {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  padding: 1.25rem;
  padding-bottom: 0;
  background-color: #f2f2f2;
  color: #282828;
  line-height: 1.3;
  font-size: 0.9375rem;
}
@media only screen and (min-width: 1000px) {
  body {
    padding: 1.5rem;
  }
}

.orange-bg {
  background: #e38826;
  border-radius: 0.5rem 0.5rem 0 0;
}

.cyan-bg {
  background: #006970;
}

.green-bg {
  background: #004241;
  border-radius: 0 0 0.5rem 0.5rem;
}

h1 {
  color: #f2f2f2;
  font-family: "Big Shoulders Display", cursive;
  text-transform: uppercase;
  font-size: 40px;
  padding: 0;
  margin: 0;
}

p {
  color: rgba(255, 255, 255, 0.75);
  font-family: "Lexend Deca", sans-serif;
  margin: 0;
  font-weight: 400;
}

a {
  text-decoration: none;
  font-family: "Lexend Deca", sans-serif;
}

.btn-orange {
  color: #e38826;
}

.btn-cyan {
  color: #006970;
}

.btn-green {
  color: #004241;
}

.btn {
  align-items: center;
  background: #f2f2f2;
  border-radius: 50px;
  padding: 15px 30px;
}
.btn:hover {
  background: transparent;
  color: #f2f2f2;
  border: 2px #f2f2f2 solid;
}

footer {
  text-align: center;
  font-family: "Lexend Deca", sans-serif;
  color: #e38826;
  font-size: 0.625rem;
}
footer a {
  text-decoration: none;
  color: #303030;
}
footer a:hover {
  color: #e38826;
}

main {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding: 3.8125rem 0;
}

.card {
  padding: 48px 2.8125rem;
  padding-bottom: 60px;
}
.card header {
  background-repeat: no repeat;
  padding-top: 0;
  padding-bottom: 25px;
}
.card p {
  padding-top: 23px;
  padding-bottom: 38px;
  line-height: 1.7;
}

@media only screen and (min-width: 760px) {
  main {
    flex-direction: row;
    flex-wrap: unset;
  }

  .orange-bg {
    border-radius: 0.5rem 0 0 0.5rem;
    margin-left: 15%;
  }

  .green-bg {
    border-radius: 0 0.5rem 0.5rem 0;
    margin-right: 15%;
  }

  .card p {
    padding-bottom: 100px;
  }
}